<!-- TreeRender -->
<template>
  <span class="rightContent">
    <template>
      <span>{{treeData.title}}</span>
      <span class="btns">
        <Button class="btn-add" type="info" size="small"  icon="md-add" @click="addModal=true, addTreeData = {}, $refs.addTreeData.resetFields()" style="margin-right: 10px">添加</Button>
        <Button type="warning" size="small" icon="md-create" @click="nodeEdit(treeRoot,treeNode,treeData)" style="margin-right: 10px">修改</Button>
        <Button v-if="treeData.parentId !== -1" type="error" size="small" icon="md-trash" @click="nodeDel(treeRoot,treeNode,treeData)">删除</Button>
      </span>
    </template>
    <Modal v-model="addModal">
      <Form ref="addTreeData" :model="addTreeData" :rules="addTreeDataValidate" :label-width="135" style="width: 80%;margin: 40px auto 0">
        <FormItem prop="title" label="人类活动类型名称：">
          <Input v-model="addTreeData.title" autofocus size="default"/>
        </FormItem>
      </Form>
      <p slot="footer">
        <Button type="primary" @click="nodeAdd(treeData, addTreeData.title)">保存</Button>
        <Button @click="addModal=false">取消</Button>
      </p>
    </Modal>
    <Modal v-model="editModal">
      <Form ref="editTreeData" :model="treeData" :rules="editTreeDataValidate" :label-width="135" style="width: 80%;margin: 40px auto 0">
        <FormItem prop="title" label="人类活动类型名称：">
          <Input v-model="treeData.title" autofocus size="default"/>
        </FormItem>
      </Form>
      <p slot="footer">
        <Button type="primary" @click="nodeEditPass(treeRoot,treeNode,treeData)">保存</Button>
        <Button @click="editModal=false">取消</Button>
      </p>
    </Modal>
  </span>
</template>

<script>
export default {
  name: 'TreeRender',
  components: {},
  data () {
    return {
      // 是否显示修改弹窗
      editModal: false,
      addModal: false,
      addTreeData: {
        title: ''
      },
      /* 添加数据表单校验 */
      addTreeDataValidate: {
        title: [
          { required: true, message: '人类活动类型名称必须填写', trigger: 'blur' }
        ]
      },
      /* 修改数据表单校验 */
      editTreeDataValidate: {
        title: [
          { required: true, message: '人类活动类型名称必须填写', trigger: 'blur' }
        ]
      }
      // isEdit: false
    }
  },
  props: {
    // 树的根节点
    treeRoot: {
      type: Array
    },
    // 当前节点
    treeNode: {
      type: Object
    },
    // 当前节点的数据
    treeData: {
      type: Object
    }
  },
  computed: {},
  methods: {
    nodeAdd (d, e) {
      this.$refs.addTreeData.validate(valid => {
        if (valid) {
          d.addData = e
          this.$emit('nodeAdd', d, e)
          this.addModal = false
        }
      })
    },
    nodeEdit (r, n, d) {
      this.editModal = true
      // d.isEdit = this.isEdit
      d.title = this.treeData.title
      // this.$set(d, 'isEdit', d.isEdit)
      this.$set(d, 'title', d.title)
      this.$emit('nodeEdit', r, n, d)
    },
    nodeEditPass (r, n, d) { // 编辑完成
      this.$refs.editTreeData.validate(valid => {
        if (valid) {
          this.editModal = false
          // d.isEdit = this.isEdit
          d.title = this.treeData.title
          // this.$set(d, 'isEdit', d.isEdit)
          this.$set(d, 'title', d.title)
          this.$emit('nodeAfterEdit', r, n, d)
        }
      })
    },
    nodeDel (r, n, d) {
      this.$emit('nodeDel', r, n, d)
    }
  },
  watch: {},
  created () {},
  mounted () {}
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  .rightContent {
    width: calc(100% - 22px);
    display: inline-block;
    overflow: hidden;
    .btns {
      float: right;
    }
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
</style>
