<template>
  <div id="activitiesType">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>人类活动变化检测详情</span>
     </div>
    <Tree :data="treeData" :render="renderContent">
    </Tree>
  </div>
</template>

<script>
import TreeRender from './TreeRender'
import {
  // 人类活动类型列表
  getActiveList,
  // 新增
  saveActiveType,
  // 修改
  updateActiveType,
  // 删除
  deleteActiveType
} from '@/api/remoteSensing'
export default {
  components: {},
  data () {
    return {
      treeData: [],
      // 树结构
      treeList: null
    }
  },
  props: {},
  computed: {},
  methods: {
    renderContent (h, { root, node, data }) {
      let that = this
      return h(TreeRender, {
        props: {
          treeRoot: root,
          treeNode: node,
          treeData: data
        },
        style: {
          borderBottom: '1px solid #d1d1d1',
          padding: '2px 0 4px'
        },
        on: {
          nodeAdd: (d) => that.handleAdd(d),
          nodeEdit: (r, n, d) => that.handleEdit(r, n, d),
          nodeDel: (r, n, d) => that.handleDelete(r, n, d),
          nodeAfterEdit: (r, n, d) => that.handleAfterEdit(r, n, d)
        }
      })
    },
    /* axios: 获取图层类型菜单 */
    initTreeList () {
      getActiveList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.treeList = res.data.data
          this.treeData = this.treeList
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 编辑后，input框失去焦点
    handleAfterEdit (r, n, d) {
      let column = {}
      column.name = d.title
      column.id = d.id
      column.parentid = d.parentId
      column.type = d.type
      column.children = []
      this.UpdateColumn(column)
    },
    // 编辑中，input框得到焦点
    handleEdit (r, n, d) {
      d.name = d.title
    },
    // 新增方法
    handleAdd (d) {
      //  构建一个实体
      let column = {}
      const children = d.children || []
      // 添加数据
      children.push({
        title: d.addData,
        expand: true,
        parent: d.id,
        // isEdit: false,
        children: []
      })
      column.name = d.addData
      column.parentid = d.id
      column.type = String(d.type + 1)
      column.children = []
      this.saveColumn(column)
    },
    saveColumn (column) {
      saveActiveType({
        data: column
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '添加成功' })
          // 完成增操作后进行一次查询
          this.initTreeList()
        } else {
          this.$Notice.error({ title: res.data.msg })
          // 完成增操作后进行一次查询
          this.initTreeList()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        // 完成增操作后进行一次查询
        this.initTreeList()
      })
    },
    UpdateColumn (column) {
      updateActiveType({
        data: column
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '修改成功' })
          // 改操作后进行一次查询
          this.initTreeList()
        } else {
          this.$Notice.error({ title: res.data.msg })
          // 改操作后进行一次查询
          this.initTreeList()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        // 改操作后进行一次查询
        this.initTreeList()
      })
    },
    handleDelete (r, n, d) {
      const parentKey = r.find(el => el === n).parent
      const parent = r.find(el => el.nodeKey === parentKey).node
      const index = parent.children.indexOf(d)
      parent.children.splice(index, 1)
      let id = d.id
      this.deleteColumn(id)
    },
    // 根据id查询
    deleteColumn (id) {
      this.$Modal.confirm({
        title: '确认',
        content: '请问您确定要删除该人类活动类型吗？',
        onOk: () => {
          deleteActiveType({
            data: {
              id: id
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '删除成功' })
              // 改操作后进行一次查询
              this.initTreeList()
            } else {
              this.$Notice.error({ title: res.data.msg })
              // 改操作后进行一次查询
              this.initTreeList()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            // 改操作后进行一次查询
            this.initTreeList()
          })
        },
        onCancel: () => {
        }
      })
    }
  },
  watch: {},
  created () {
    this.initTreeList()
  },
  mounted () {}
}
</script>

<style scoped lang="scss">
  #activitiesType {
    position: relative;
    .ivu-tree {
      padding: 0 2%;
      margin: 10px auto 0;
      text-align: left;
      overflow: auto;
      height: calc(100% - 90px);
      .ivu-tree-arrow {
        float: left !important;
        margin-right: 10px;
      }
    }
  }
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    line-height: 50px;
    text-align: right;
    .title {
      float: left;
      font-size: 14px;
      i {
        vertical-align: middle;
        font-size: 20px;
        margin-right: 5px;
      }
    }
  }
</style>
<style lang="scss">
  #activitiesType {
    .ivu-tree {
      li{
        margin: 2px;
      }
    }
  }
</style>
